@import 'assets/scss/utils/mixins';

.icon-18px {
	display: inline-block !important;
	width: 18px;
	height: 18px;
	font-size: 18px;
	overflow: unset;
}

.tenant-item {
	border: 1px solid var(--general-2);
	border-radius: 8px;
	min-height: 450px;
}

.limit-2 {
	overflow: hidden;
	text-overflow: ellipsis;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical;
}

.mw-343px {
	max-width: 343px;
}

.view-date {
	background-color: rgba(255, 90, 184, 0.08);
	border: 1px solid var(--prim-2);
	border-radius: 4px;
}

.tenant-img {
	border: 1px solid var(--general-1);
	border-radius: 50%;
	object-fit: cover;
}

.on-hunt-badge {
	position: absolute;
	bottom: 0;
}

.avatar {
	width: 100px;
	height: 100px;
	border: 5px solid var(--basic-1);
	border-radius: 50%;
}
